import { DateTime } from "luxon";
import worldCountries from "shared/constants/worldCountries";
import { T_ClockDataOfTimeZone } from "./types";

export function getClocksDataOfLocalAndOtherTimeZones(otherTimeZones?: string[]) {
  const now = DateTime.now();
  const clocksData = [getClockDataOfTimeZone(now)];
  if (otherTimeZones) {
    clocksData.push(
      ...otherTimeZones.map((tz) => getClockDataOfTimeZone(now, tz))
    );
  }
  return clocksData;
}

export function getClockDataOfTimeZone(
  datetime: DateTime,
  timeZone?: string
): T_ClockDataOfTimeZone {
  const nowInTimeZone = timeZone ? datetime.setZone(timeZone) : datetime;
  const {
    // timeZone
    zoneName,
    offsetNameLong,
    offsetNameShort,
    // time
    hour,
    minute,
    second,
    isInDST,
    // date
    year,
    monthShort,
    monthLong,
    day,
    weekdayLong,
    weekdayShort
  } = nowInTimeZone;

  const timeZoneName = standardizeTimeZoneName(zoneName);
  const meridiem = nowInTimeZone.toFormat("a");
  const countryInfo = worldCountries.find(
    (info) => info.timeZone === timeZoneName
  ) || {
    countryCode: "",
    countryName: ""
  };

  return {
    timeISOString: datetime.toISO(),
    isLocal: !timeZone,
    timeZoneName,
    offsetNameLong,
    offsetNameShort,
    ...countryInfo,
    hour: nowInTimeZone.toFormat("hh"),
    minute: nowInTimeZone.toFormat("mm"),
    second: nowInTimeZone.toFormat("ss"),
    meridiem,
    isInDST,
    year,
    monthShort,
    monthLong,
    day,
    weekdayLong,
    weekdayShort,
    hourDeg: (hour / 12) * 360 + (minute / 60) * 30 + 90,
    minuteDeg: (minute / 60) * 360 + (second / 60) * 6 + 90,
    secondDeg: (second / 60) * 360 + 90
  };
}

export const localTimeZoneName = standardizeTimeZoneName(DateTime.now().zoneName);

export function standardizeTimeZoneName(timeZoneName: string) {
  return timeZoneName.replace(
    // TODO: Stupid Chrome https://bugs.chromium.org/p/chromium/issues/detail?id=580195
    "Calcutta",
    "Kolkata"
  );
}
