import { useEffect, useState } from "react";

import { T_ClocksDataState } from "shared/helpers/timeZone/types";
import { getClocksDataOfLocalAndOtherTimeZones, localTimeZoneName } from "shared/helpers/timeZone";

export function useClocksData(otherTimeZones?: string[]) {
  const otherTimeZonesOnly = otherTimeZones?.filter(
    (tz) => localTimeZoneName !== tz
  );

  const [state, setState] = useState<T_ClocksDataState>(
    getClocksDataOfLocalAndOtherTimeZones(otherTimeZonesOnly)
  );

  useEffect(() => {
    const intervalId = setInterval(() => {
      setState(getClocksDataOfLocalAndOtherTimeZones(otherTimeZonesOnly));
    }, 1000);
    return () => clearInterval(intervalId);
    // otherTimeZonesWithoutLocal is computed based on otherTimeZones
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [otherTimeZones]);

  return state;
}
